// Make a Gameboard class that contains all of the guesses - for the state
class GameBoard {
  constructor() {
    this.rows = 6;
    this.cols = 5;
    this.board = [];
    // Set up grid
    for (let i = 0; i < this.rows; i++) {
      this.board[i] = [];
      for (let j = 0; j < this.cols; j++) {
        this.board[i][j] = "";
      }
    }
  }

  getBoard = () => this.board;

  addLetter(row, column, letter) {
    this.board[row][column] = letter;
  }

  deleteLetter(row, column) {
    this.board[row][column] = "";
  }

  printBoard() {
    for (let i = 0; i < this.rows; i++) {
      console.log(this.board[i]);
    }
  }
}

