// Game Controller to handle logic - for the controller
class GameController {
  constructor(gameBoard) {
    this.board = gameBoard;
    this.guessNum = 0;
    this.position = 0;
    this.answerLength = 5;
  }
  // Check if key.event is an English letter
  isLetter(letter) {
    return /^[a-zA-Z]$/.test(letter);
  }
  // Check if key.event is a Korean letter
  isKorean(letter) {
    const koreanRegex = /[\u3130-\u318F\uAC00-\uD7AF]+/;
    return koreanRegex.test(letter);
  }
  // If key.event is English, convert to Korean Letter
  convertToKorean(letter) {
    const englishToKorean = {
      q: "ㅂ",
      w: "ㅈ",
      e: "ㄷ",
      r: "ㄱ",
      t: "ㅅ",
      y: "ㅛ",
      u: "ㅕ",
      i: "ㅑ",
      o: "ㅐ",
      p: "ㅔ",
      a: "ㅁ",
      s: "ㄴ",
      d: "ㅇ",
      f: "ㄹ",
      g: "ㅎ",
      h: "ㅗ",
      j: "ㅓ",
      k: "ㅏ",
      l: "ㅣ",
      z: "ㅋ",
      x: "ㅌ",
      c: "ㅊ",
      v: "ㅍ",
      b: "ㅠ",
      n: "ㅜ",
      m: "ㅡ",
    };
    if (englishToKorean[letter.toLowerCase()]) {
      return englishToKorean[letter.toLowerCase()];
    }
  }
  // handle any user input from event listeners
  handleEvent(key) {
    if (key === "Backspace" || key === "Delete") {
      console.log("delete");
      //this.gameController.deleteLetter();
    } else if (key === "Enter" || key === "ENTER") {
      console.log("enter");
      //this.gameController.checkWord();
    } else if (this.isKorean(key)) {
      console.log("added");
      //this.gameController.addLetter(key);
    } else if (this.isLetter(key)) {
      console.log("added in korean");
      //this.gameController.addLetter(convertToKorean(key));
    }
  }

  handleLetter(letter) {
    if (this.position < this.answerLength) {
      this.board.addLetter(this.guessNum, this.position, letter);
      this.position++;
    }
  }

  deleteLetter(letter) {
    if (this.position >= 0) {
      // Not on the last column of the guess
      if (this.position === this.answerLength) {
        this.position--;
      }
      this.board.deleteLetter(this.guessNum, this.position);
      this.position--;
    }
  }

  printBoard() {
    console.log("This is the state of the board:");
    this.board.printBoard();
  }
}

class ScreenController {
  constructor(gameController) {
    // DOM Elements
    this.body = document.body;
    this.keys = document.querySelectorAll(".key");
    this.listenToEvents();
    this.letters = document.querySelectorAll(".letter");
    this.spinner = document.querySelector(".spinner");
    // Color object
    this.colors = {
      white: "#FFFFFF",
      black: "#000000",
      green: "#6AA964",
      yellow: "#C9B458",
      gray: "#787C7E",
      ltgray: "#D3D6DA",
    };
    // GameController
    this.gameController = gameController;
  }
  // Set loading icon visible or hidden depending on AJAX request
  setLoading(isLoading) {
    isLoading
      ? (spinner.style.visibility = "visible")
      : (spinner.style.visibility = "hidden");
  }
  // Listen to whatever the user presses on their keyboard
  listenToEvents() {
    this.body.addEventListener("keydown", (event) => {
      this.gameController.handleEvent(event.key);
    });
    // Listen to the key buttons for user input
    this.keys.forEach((key) => {
      key.addEventListener("click", () => {
        this.gameController.handleEvent(key.innerText);
      });
    });
  }
}

function init() {
  const board = new GameBoard();
  const game = new GameController(board);
  const screen = new ScreenController(game);
}

init();
